<template>
    <div class="bgcontainer"
        style="background-image:url(static/img/login-bg.png)"
        v-resize="onResize">
        <div class="logo"
            style="background-image:url(static/img/logo.png)"></div>
        <v-text-field
            v-model="username"
            class="user"
            label="用户"
            prepend-icon="person"
          ></v-text-field>
        <v-text-field
            v-model="password"
            class="user"
            type="password"
            label="密码"
            prepend-icon="keyboard_return"
          ></v-text-field>
          <v-btn color="#BFC7D4" class="login"
            @click.native="login">
              <v-icon dark>check</v-icon>
              登录
          </v-btn>
        <div class="footer" v-show="showfooter">
            舟山引航站 引航综合数据平台
            <div class="copyright">copyright&copy; 2018</div>
        </div>
    </div>
</template>

<script>
import {mapActions} from 'vuex';
import util from '../util/util.js';
import localjson from '../util/localjson.js';

export default {
    name: 'login',
    data() {
        return {
            password: '',
            username: '',
            showfooter: true,
        };
    },
    methods: {
        async login() {
            if(this.username.length == 0 || this.password.length == 0) return;
            const userinfo = await util.login(this, this.username, this.password);
            util.token = userinfo.token;
            localjson.setUser(userinfo);
            this.$store.commit('setUser', userinfo);
            this.$router.push('/notice');
        },
        onResize () {
            this.showfooter = window.innerHeight > 400;
        }
    },
    mounted() {
        this.password = '';
        this.username = '';
        this.$store.commit('setAppHeader', {
            show: false,
        });
        this.$store.commit('setEnableSwipeOut', false);
        this.$store.commit('setShowBottom', false);
    },
};
</script>

<style scoped>
.bgcontainer{
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment:fixed;
    background-size:cover !important; 
    height: 100% !important;
}

    .logo{
        background-repeat: no-repeat;
        background-position: center center;
        background-position-y: 50px;
        height: 220px;
        width: 100%;
    }
    
    .user{
width: 220px;
        margin: auto;
        margin-top: 16px;
    }
    
    .login {
        width: 220px;
        left: 50%;
        margin: 0;
        transform: translateX(-50%);
    }
        

    .footer{
position: absolute;
        bottom: 0;
        height: 70px;
        margin-right: auto;
        font-size: 12px;
        color: rgb(64,64,64);
        text-align: center;
        width: 100%;
    }
        

        .copyright{
font-size:10px;
        }
            
</style>



